* {
  margin: 0;
  padding: 0;
  outline: 0;
  box-sizing: border-box;
}
html {
  height: 100vh;
  width: 100vw;
}
body {
  height: 100%;
  width: 100%;
  background: #383838;
  
}
body,
input,
button {
  font: 14px sans-serif;
  font-family: "Roboto", sans-serif;
}
#root {
  width: 100%;
  height: 100%;
}
button {
  cursor: pointer;
}
body {
  -webkit-font-smoothing: antialiased;
}
